import React from 'react';
import "./Social.css";
import {FaGithub, FaInstagram} from "react-icons/fa";

function Social() {
    return (
        <div className="social-overlay">
            <ul className="social-overlay-list">
                <li className="social-overlay-item">
                    <a target="_blank" className="social-overlay-item-icon-holder" href="https://github.com/matteolutz"><FaGithub className="social-overlay-item-icon" /></a>
                </li>
                <li className="social-overlay-item">
                    <a target="_blank" className="social-overlay-item-icon-holder" href="https://instagram.com/lu.matteo.1"><FaInstagram className="social-overlay-item-icon" /></a>
                </li>
            </ul>
        </div>
    );
}

export default Social;